@white-color: #fff; //纯白
@font-gray-color: #b3b3b3; //字体灰色
@font-blue-color: #1875f0; //字体蓝色 
@theme-color: #ac103d; //主题红
@btn-color: #505766; //按钮灰
.paginationCommon {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.btn-link-blue {
  color: @font-blue-color;
}

.btn-link-grey {
  color: @font-gray-color;
}

.ant-divider-horizontal {
  margin: 10px 0;
}

.job-order-modify-page {
  .ant-table-small {
    .ant-table-thead > tr > th {
      background-color: #f6f9fd;
      padding: 4px 8px;
      text-align: center !important;
    }

    .ant-table-tbody > tr.ant-table-row-selected > td {
      background-color: #edf1f6;
    }
  }
}

.paginationCommonRight {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.grayBtn {
  border-color: @btn-color;
  background: @btn-color;
}

.tablecommon {
  .ant-table-container {
    border-top: #505766 solid 1px;
  }
  .ant-table-row:hover > td {
    background-color: #edf1f7 !important;
  }
  .ant-table-thead {
    tr {
      th {
        background-color: #f7f9fd;
      }
    }
  }
}

::selection {
  background: #bbdefb;
}
