@import "../../style/index.less";

.cautionRecord {
  padding: 16px;
  overflow-y: scroll;
  height: 100%;
  .ant-form-item {
    margin-bottom: 14px;
  }
  .btn {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10px;

    .ant-btn-sm {
      margin-right: 10px;
    }
  }
  .ant-btn .anticon {
    vertical-align: unset;
  }
  .pagination {
    .paginationCommonRight();
  }
  .linkicon {
    color: @font-blue-color;
    cursor: pointer;
  }
  .ant-picker {
    width: 100%;
  }
  .editForm {
    margin: 0;
    width: 100px;
  }
  .grayBtn {
    .grayBtn();
  }
  .noline {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    word-break: break-all;
  }
  .tablecommon();
}

.import-modal-content {
  display: flex;
  flex-direction: column;
  margin: 20px;

  > div {
    > label {
      padding-right: 6px;
    }
  }

  > .action-btn {
    text-align: right;
    margin-top: 40px;
    margin-bottom: 20px;
  }
}

@primary-color: #ac103d;