@import '../../style/index.less';

.verificationSMS{
  padding: 40px;
  height: 100%;
  .title{
    font-size: 18px;
    padding-bottom: 20px;
  }
  .btn{
    display: flex;
    justify-content: flex-end;
    padding-bottom: 20px;
  }
  .sendSMS{
    color: @theme-color;
    cursor:pointer;
    .coutdown{
      display: flex;
    }
  }
  .ant-spin-nested-loading{
    height: 100%;
    .ant-spin-container{
      height: 100%;
    }
  }
  .toBottomBtn{
    position: absolute;
    right: 0;
    bottom: 0;
  }
  .redcolorsty{
    color: @theme-color;
  }
 
}

.btnGroupAml{
  display: flex;
  justify-content: flex-end;
}
@primary-color: #ac103d;