@import '../../style/index.less';

.amlDetail{
  padding: 16px;
  .ant-form-item{
    margin-bottom: 10px;
  }
  .ant-divider-horizontal{
    margin: 8px 0;
  }
  .title{
    font-size: 18px;
    padding-bottom: 20px;
  }
  .checkSty{
    color: @font-blue-color;
    padding: 4px 10px;
    cursor:pointer;
  }
  .uploadtitle{
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
  }
  .btn{
    display: flex;
    justify-content: flex-end;
  }
  .uptable{
    margin-bottom: 50px;
  }
  .footerBtn{
    padding: 10px;
    background: @white-color;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow:0px 0px 10px @font-gray-color;
    display: flex;
    justify-content: flex-end;
  }
  .tablecommon()
}
@primary-color: #ac103d;